import React from 'react'
import { YMaps, Map, Placemark } from 'react-yandex-maps'

const mapState = { center: [53.953546, 27.616116], zoom: 16 }
const coordinates = [[53.953546, 27.616116]]

export const Contacts = ({ contacts }) => {
  return (
    <footer className="footer" id="footer">
      <h1>Контакты:</h1>
      <div className="contacts">
        <div className="contacts-text">
          <div className="contacts-item">
            <img src={contacts.lifeIMG} alt={contacts.altLife} />
            <img src={contacts.telegramIMG} alt={contacts.altTelegram} />
            <a href={`tel: ${contacts.tel}`}>{contacts.tel}</a>
          </div>
          <div className="contacts-item">
            <img src={contacts.instagramIMG} alt={contacts.altInstagram} />
            <a href={contacts.inst} target="_blank" rel="noreferrer">
              bmw__diagnostics
            </a>
          </div>
          <div
            className="contacts-text"
            // style={{ marginTop: '30px', maxWidth: '50%', fontSize: '18px' }}
          >
            {contacts.text}
          </div>
        </div>
        <div className="contacts-map">
          <YMaps>
            <Map defaultState={mapState}>
              {coordinates.map((coordinate) => (
                <Placemark geometry={coordinate} />
              ))}
            </Map>
          </YMaps>
        </div>
      </div>
    </footer>
  )
}
