import React, { useState } from 'react'
import axios from 'axios'

export const About = ({ about }) => {
  const [name, setName] = useState('')
  const [phone, setPhone] = useState('')
  const [description, setDescription] = useState('')

  const sendToBack = (e) => {
    e.preventDefault()
    alert('test')
    // axios.get('/http', {
    //   name: name,
    //   phone: phone,
    //   description: description,
    // })
  }

  return (
    <section class="about" id="about">
      <p class="about-p">
        <img src={about.img} alt={about.alt} />
        <span class="about-p-text">{about.text}</span>
        <div>
          <h1
            style={{
              margin: 'auto',
              textAlign: 'center',
              fontSize: '26px',
              marginBottom: '15px',
            }}
          >
            Обратная связь:
          </h1>
          <form className="form" onSubmit={sendToBack}>
            <label className="form_label">
              Ваше имя:
              <input
                className="form_input"
                onChange={(e) => setName(e.target.value)}
                type="text"
                name="name"
              />
            </label>
            <label className="form_label">
              Номер телефона:
              <input
                onChange={(e) => setPhone(e.target.value)}
                type="tel"
                name="phone"
              />
            </label>
            <label className="form_label">Опишите проблемму:</label>
            <textarea
              className="form_area"
              onChange={(e) => setDescription(e.target.value)}
            ></textarea>
            <button
              className="form_submit"
              style={{
                minWidth: '100px',
                minHeight: '35px',
                margin: '15px auto',
                borderRadius: '10px',
              }}
              type="submit"
              value="Отправить"
              // onclick={sendToBack}
            >
              Отправить
            </button>
            {/* <input
              className="form_submit"
              style={{
                minWidth: '100px',
                minHeight: '35px',
                margin: '15px auto',
                borderRadius: '10px',
              }}
              type="submit"
              value="Отправить"
              onclick={sendToBack}
            /> */}
          </form>
        </div>
      </p>
    </section>
  )
}
