import React from 'react'
import { Card } from './Card'

export const Content = ({ content }) => {
  const Cards = content.map((el) => (
    <Card
      key={el.id}
      className={el.className}
      name={el.name}
      description={el.description}
      alt={el.alt}
      img={el.img}
    />
  ))
  return (
    <section className="content" id="content">
      {Cards}
    </section>
  )
}
