import React from 'react'
import logo from '../img/logo.png'

export const Navbar = () => {
  return (
    <nav className="navbar" role="navigation">
      <img src={logo} alt="logo"></img>
      <ul className="menu__box">
        <li>
          <a className="menu__item" href="#main">
            Главная
          </a>
        </li>
        <li>
          <a className="menu__item" href="#content">
            Услуги
          </a>
        </li>
        <li>
          <a className="menu__item" href="#about">
            О нас
          </a>
        </li>
        <li>
          <a className="menu__item" href="#footer">
            Контакты
          </a>
        </li>
      </ul>
    </nav>
  )
}
